export default {
	data() {
		return {
			all:null,
		}
	},
	onLoad()
	{
		this.userall();
	},
	methods: {
		userall() {
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/userall',
				data: {
					session_key: uni.getStorageSync('storage_key')
				},
				method: 'POST',
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 200) {
						_this.all = res.data.data;
					}
				},
				fail: (res) => {
					console.log(res.data);
				}
			});
		}
	}
}
